import styled from "styled-components"
import { Container as bsContainer } from "react-bootstrap"

export const Container = styled(bsContainer)`
	margin-bottom: 56px;

	input[type="checkbox"],
	input[type="radio"] {
		margin-right: 8px;
	}

	label {
		font-weight: 600;
		font-family: "Montserrat", sans-serif;
		margin-bottom: 0;
	}

	.row {
		margin-bottom: 16px;
	}

	p {
		margin: 16px 0 4px 0;
		font-weight: 600;
		font-family: "Montserrat", sans-serif;
	}
`

export const Button = styled.button`
	width: 100%;
	font-size: 17px;
	font-weight: 600;
	color: #fff;
	background: #ff700e;
	border: 0;
	border-radius: 5px;
	padding: none;
	height: 60px;
	margin-top: 5px;
	outline: 0 !important;
`
