import Service from "./service"
import { IJobFormData, IJobs } from "../interfaces"

class JobService {
	private api = new Service()

	public list<T>(): Promise<T> {
		return this.api.get("job")
	}

	public get<T>(id: number): Promise<T> {
		return this.api.get(`job/${id}`)
	}

	public delete(id: number): Promise<void> {
		return this.api.delete(`job/${id}`)
	}

	public create({
		title,
		image,
		content,
		published,
		userId = "0",
	}: IJobFormData) {
		return this.api.post("job", {
			title,
			description: content,
			published,
			imageData: image,
			authorId: userId,
		})
	}

	public update({
		id,
		title,
		image,
		imageId,
		content,
		published,
		userId = "0",
	}: IJobFormData) {
		return this.api.put(`job/${id}`, {
			id,
			title,
			imageId,
			imageData: image,
			description: content,
			published,
			authorId: userId,
		})
	}

	public savePreview(job: IJobs) {
		if (typeof localStorage != "undefined") {
			localStorage.setItem("jobPreview", JSON.stringify(job))
		}
	}

	public getPreview(): IJobs | undefined {
		if (typeof localStorage != "undefined") {
			const article = localStorage.getItem("jobPreview")

			if (!article) {
				return undefined
			}

			return JSON.parse(article)
		}
	}
}

export default JobService
