import React, { useCallback, useRef, useState } from "react"
import { FormHandles } from "@unform/core"
import { Form } from "@unform/web"
import { Row, Col } from "react-bootstrap"
import * as Yup from "yup"
import { navigateTo } from "gatsby"

import { translate } from "../../translate"
import { Container, Button } from "./styles"
import Input from "../Input"
import Textarea from "../Textarea"
import getValidationErrors from "../../../../utils/getValidationErrors"
import CheckboxInput from "../Checkbox"
import { sendEmail } from "../../../../services/form-service"
import FileInput from "../InputFile"
import RadioInput from "../Radio"

interface ICheckboxOption {
	id: string
	value: string
	label: string
}

const FormCareer: React.FC = () => {
	const formRef = useRef<FormHandles>(null)
	const [loading, setLoading] = useState(false)

	const checkboxInterest: ICheckboxOption[] = [
		{
			id: "InterestBudgeOp1",
			value: "Administração",
			label: translate("form.Administração"),
		},
		{
			id: "InterestBudgeOp2",
			value: "Analista de Controle da Qualidade",
			label: translate("form.Analista de Controle da Qualidade"),
		},
		{
			id: "InterestBudgeOp3",
			value: "Analista da Garantia da Qualidade",
			label: translate("form.Analista da Garantia da Qualidade"),
		},
		{
			id: "InterestBudgeOp4",
			value: "Projetista - Arquitetura AutoCAD",
			label: translate("form.Projetista - Arquitetura AutoCAD"),
		},
		{
			id: "InterestBudgeOp5",
			value: "Ministrante de Cursos",
			label: translate("form.Ministrante de Cursos"),
		},
		{
			id: "InterestBudgeOp6",
			value: "Comercial",
			label: translate("form.Comercial"),
		},
		{
			id: "InterestBudgeOp7",
			value: "Serviços Gerais/Limpeza",
			label: translate("form.Serviços Gerais/Limpeza"),
		},
		{
			id: "InterestBudgeOp8",
			value: "Supervisão/Gestão",
			label: translate("form.Supervisão/Gestão"),
		},
		{
			id: "InterestBudgeOp9",
			value: "Recepção/Atendimento",
			label: translate("form.Recepção/Atendimento"),
		},
		{
			id: "InterestBudgeOp10",
			value: "Outro",
			label: translate("form.Outro"),
		},
	]

	const radioOptionsYesNo: ICheckboxOption[] = [
		{
			id: "opYes",
			value: "sim",
			label: translate(`form.yes`),
		},
		{
			id: "opNo",
			value: "não",
			label: translate(`form.no`),
		},
	]

	const checkboxIAgree: ICheckboxOption[] = [
		{
			id: "concordoFormulario",
			value:
				"Eu aceito receber e-mails com noticias, conteúdos e contatos da Kivalita Consulting",
			label: translate("form.i agree form"),
		},
	]

	const handleSubmit = useCallback(async data => {
		try {
			formRef.current?.setErrors({})

			const schema = Yup.object().shape({
				nome: Yup.string().required("Nome obrigatório"),
				email: Yup.string()
					.required("E-mail obrigatório")
					.email("Digite um e-mail válido"),
				telefone: Yup.string().required("Telefone obrigatória"),
				mensagem: Yup.string().required("Mensagem obrigatória"),
				outraLingua: Yup.string().required("Campo obrigatório"),
				veiculoProprio: Yup.string().required("Campo obrigatório"),
				disponibilidadeViagem: Yup.string().required("Campo obrigatório"),
				areasDeInteresse: Yup.string().required("Campo obrigatório"),
				concordoFormulario: Yup.string().required("Campo obrigatório"),
			})

			await schema.validate(data, { abortEarly: false })

			setLoading(true)

			await sendEmail(data)

			navigateTo("/thank-you-career")
		} catch (err) {
			setLoading(false)
			if (err instanceof Yup.ValidationError) {
				const errors = getValidationErrors(err)
				formRef.current?.setErrors(errors)

				return
			}

			navigateTo("/thank-you-career")
		}
	}, [])

	return (
		<Form ref={formRef} onSubmit={handleSubmit}>
			<Container className="containerForm">
				<Row>
					<Col xs="12" md="12" lg="12">
						<label>{translate(`form.Nome Completo`)}</label>
						<Input name="nome" />
					</Col>
				</Row>

				<Row>
					<Col xs="12" md="6" lg="6">
						<label>{translate(`form.E-mail`)}</label>
						<Input type="email" name="email" />
					</Col>
					<Col xs="12" md="6" lg="6">
						<label>{translate(`form.Telefone`)}</label>
						<Input name="telefone" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.interesting")}</p>
						<CheckboxInput
							name="areasDeInteresse"
							options={checkboxInterest}
						></CheckboxInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.otherLang")}</p>
						<RadioInput
							name="outraLingua"
							options={radioOptionsYesNo}
						></RadioInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.vehicle")}</p>
						<RadioInput
							name="veiculoProprio"
							options={radioOptionsYesNo}
						></RadioInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<p>{translate("form.travel")}</p>
						<RadioInput
							name="disponibilidadeViagem"
							options={radioOptionsYesNo}
						></RadioInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label>{translate(`form.skills`)}</label>
						<Textarea name="mensagem" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label style={{ width: "100%" }}>
							{translate(`form.uploadResume`)}
						</label>
						<FileInput name="arquivo" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<CheckboxInput
							name="concordoFormulario"
							options={checkboxIAgree}
						></CheckboxInput>
					</Col>
				</Row>

				<Button type="submit">
					{loading
						? translate("form.Enviando")
						: translate("form.Enviar Contato")}
				</Button>
			</Container>
		</Form>
	)
}

export default FormCareer
