import React from "react"

// import { Container } from './styles';

import Banner from "../../utils/Banner"
import Intro from "../../utils/IntroductionContent"
import FormCareer from "../../utils/Form/FormCareer"
import AvailableJobs from "./components/AvailableJobs"
import { translate } from "../../utils/translate"

export default function Career() {
	return (
		<div>
			<Banner titlePage={translate("banner.jobs")} />
			<Intro
				title={translate("introduction.jobs.title")}
				subtitle={translate("introduction.jobs.subTitle")}
			/>
			<AvailableJobs />
			<Intro
				title={translate("introduction.career.title")}
				subtitle={translate("introduction.career.subTitle")}
				content={translate("introduction.career.content")}
			/>
			<FormCareer />
		</div>
	)
}
