import styled from "styled-components"

export const Container = styled.div`
	background-color: transparent;
	padding: 32px 64px;
	margin-top: 62px;
	text-align: center;

	@media (max-width: 576px) {
		padding: 32px 16px;
	}

	p {
		color: #444;
		font-family: "Montserrat", Sans-serif;
		font-size: 18px;
		font-weight: 300;
		line-height: 1.6em;
	}
`

export const Subtitle = styled.h3`
	color: #000000;
	font-family: "Montserrat", Sans-serif;
	font-size: 12px;
	font-weight: 300;
	text-transform: uppercase;
	letter-spacing: 7px;
`

export const Title = styled.h2`
	color: #444;
	font-family: "Montserrat", Sans-serif;
	font-size: 35px;
	font-weight: 600;
	margin-top: 24px;
	position: relative;
	margin-bottom: 60px;

	::before {
		content: "";
		margin: 0 auto;
		position: absolute;
		height: 2px;
		width: 100px;
		background-color: #f4bf8c;
		bottom: -30px;
		left: 0;
		right: 0;
	}
`
