import React, { useState, useEffect } from "react"
import { Container, Row } from "react-bootstrap"

import JobService from "../../../../../services/job-service"
import { IJobs } from "../../../../../interfaces"
import CardJobs from "../CardJobs"
import CardJobsLoading from "../CardJobsLoading"
import { ButtonLink } from "./style"
import { translate } from "../../../../utils/translate"

const AvailableJobs: React.FC = () => {
	const [loading, setLoading] = useState(false)
	const [jobs, setJobs] = useState<IJobs[]>([])
	const jobService = new JobService()

	useEffect(() => {
		setLoading(true)
		jobService.list<IJobs[]>().then(response => {
			setJobs(response.slice(0, 4))
			setLoading(false)
		})
	}, [])

	return (
		<Container>
			<Row>
				{loading && <CardJobsLoading />}
				{jobs.map(job => (
					<CardJobs key={job.id} {...job} />
				))}
			</Row>
				<ButtonLink to="/jobs">{translate('jobs.seeMore')}</ButtonLink>
		</Container>
	)
}

export default AvailableJobs
