import styled from "styled-components"
import { Link } from "gatsby"

export const ButtonLink = styled(Link)`
	display: block;
	width: 170px;
	background-color: #115964;
	padding: 12px 24px;
	margin: 0 auto;
	border-radius: 3px;

	font-family: "Montserrat", Sans-serif;
	font-weight: 500;
	font-size: 15px;
	text-align: center;
	color: #fff !important;
	line-height: 15px;

	&:hover {
		text-decoration: none;
	}
`
