import React from "react"

import CareerComponent from "../components/pages/Career"
import Layout from "../components/Layout"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const Career = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "career",
			name: translate("breadCrumb.career"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Desenvolver sua carreira"
				description="Venha desenvolver sua carreira na Kivalita Consulting. Deixe seu currículo e preencha os dados para fazer parte do nosso banco de talentos."
			></SEO>
			<CareerComponent />
		</Layout>
	)
}

export default Career
